<template>
  <div>
    <div class="wrapp">
      <p class="title">Ссылка</p>
      <p class="title">Название</p>
      <p class="title">Дата создания</p>
    </div>
    <div v-for="elem in getDocs" :key="elem" class="doc">
      <p>{{ elem.file }}</p>
      <p>{{ elem.remark }}</p>
      <p>{{ elem.uploaded_at.split("T")[0] }}</p>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import store from "@/store";
import { mapGetters } from "vuex";

export default {
  name: "ReportsPage",
  async created() {
    await axios
      .get("https://ecotextile.by/v1/documents")
      .then(function (response) {
        store.commit("setDocs", response.data.results);
        window.console.log(response.data);
      });
  },
  computed: {
    ...mapGetters(["getDocs"]),
  },
};
</script>

<style scoped>
.wrapp {
  display: grid;
  grid-template-columns: 50% 30% 30%;
}

.doc {
  display: grid;
  grid-template-columns: 50% 30% 30%;
}

.title {
  font-size: 28px;
}
</style>
