<template>
  <div class="wrapperCenter">
    <input type="text" v-model="this.username" placeholder="Логин" />
    <input type="text" v-model="this.password" placeholder="Пароль" />
    <button @click="auth()">Войти</button>
  </div>
</template>

<script>
import store from "@/store";

export default {
  name: "usernamePage",
  data() {
    return {
      username: null,
      password: null,
    };
  },
  methods: {
    // eslint-disable-next-line vue/no-dupe-keys
    auth() {
      if (this.username === localStorage.getItem("user")) {
        store.commit("changeCheck", true);
        store.commit("setUsername", this.username);
        this.$router.push({ name: "home" });
      } else {
        store.commit("changeCheck", false);
      }
    },
  },
};
</script>

<style lang="scss" module></style>
